
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
Flexbox, FlexboxItem, XInput, Group, XButton, Alert, Divider, Blur
},
data () {
return {
Loading: false,  //按钮部分loading
disabled: false, //设置按钮不可点击
userName: '',
passWord: '',
userNameValue: '',
passWordValue: '',
show: false, 	//错误弹出框显示和标题
title: "提示",  //错误弹窗的title
content: '', 	//错误弹窗的内容
Logo: "http://appdev.pujiajia.com/static/mobile/images/app_ico_touch.png",
LoginBg: "https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg",
userNamePlaceholder: "请输入用户名",
passwordPlaceholder: "请输入密码",
VerinFormation: "冷王智能科技 V1.0.1"
}
},
methods: {

submitValid(){
this.Loading = true;
this.disabled = true;
if(this.userNameValue!=""&&this.passWordValue!=""){
this.$router.push('/index');
}else{
this.show = true;
this.content = "请输入正确的用户名和密码！";
this.Loading = false;
this.disabled = false;
}
}
}
}



